.footer {
  height: 700px;
  position: relative;
}

.footer-container {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin-left: auto;
  margin-right: auto;
  z-index: 30;
  text-align: center;
  padding: 100px 40px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: var(--clr-brwn-1);
}

.footer-container h2 {
  font-size: 25px;
}

.footer-container h3 {
  font-size: 20px;
  margin-top: 70px;
}

.footer-links {
  display: flex;
  justify-content: space-evenly;
  margin-top: 50px;
}
.footer-link {
  text-decoration: none;
  color: var(--clr-brwn-2);
}
.footer-link img {
  width: 25px;
  float: left;
}
.footer-link span {
  font-family: "Lato-bold";
  font-size: 20px;
  float: left;
  margin-left: 20px;
}
.footer-link:hover span {
  text-decoration: underline;
  color: var(--clr-brwn-3);
}

.footer-email {
  margin-top: 10px;
}
.footer-email a {
  color: var(--clr-brwn-3);
  font-family: "Lato-regular";
}

/* Extra large device (1200px - ....) */
@media screen and (min-width: 1200px) {
  .footer-container {
    width: 80%;
    max-width: 950px;
  }
}

/* Large & medium device (480px - 1200px) */
@media screen and (max-width: 1200px) {
  .footer-container {
    width: 80%;
  }
  .footer-links {
    flex-direction: column;
    width: 200px;
    margin: 30px auto;
  }
  .footer-link {
    margin-top: 20px;
  }
}

/* Small device (0px - 480px) */
@media screen and (max-width: 480px) {
  .footer-container {
    width: 100%;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
  }
}
