.hero {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  padding-bottom: 80px;
}

.hero-text {
  padding: 20px 40px;
}

.hero-image {
  padding: 20px 40px;
}

.primary-header {
  font-family: "Lato-black";
  font-size: 40px;
  color: var(--clr-whit-1);
  letter-spacing: 1px;
  margin-top: 5px;
}

.secondary-header {
  font-family: "Lato-bold";
  font-size: 25px;
  color: var(--clr-brwn-2);
  letter-spacing: 1px;
  margin-top: 50px;
}

.hero-article {
  font-family: "Lato-regular";
  font-size: 15px;
  color: var(--clr-brwn-1);
  letter-spacing: 2px;
  line-height: 1.8;
  margin-top: 50px;
}

.primary-article a {
  font-family: "Lato-bold";
  color: var(--clr-brwn-1);
}

.secondary-article {
  margin-top: 30px;
}

.cta {
  margin-top: 50px;
  display: flex;
}

.cta-btn {
  background-color: var(--clr-brwn-2);
  padding: 19px 65px;
  text-decoration: none;
  color: var(--clr-whit-1);
  border-radius: 5px;
  position: relative;
  /* Transition */
  -webkit-transition: background-color 0.1s linear;
  -moz-transition: background-color 0.1s linear;
  -ms-transition: background-color 0.1s linear;
  -o-transition: background-color 0.1s linear;
  transition: background-color 0.1s linear;
}
.cta-btn span {
  font-family: "Lato-regular";
  letter-spacing: 1px;
  background-color: transparent;
  position: absolute;
  bottom: 11px;
  left: 50px;
  font-size: 13px;
}
.cta-btn img {
  background-color: transparent;
  width: 16px;
  position: absolute;
  bottom: 12px;
  left: 20px;
}
.cta-btn:hover {
  background-color: #ac9b94;
}

.cta-link {
  margin-left: 30px;
  padding: 19px 80px;
  text-decoration: none;
  color: var(--clr-brwn-2);
  border-radius: 5px;
  position: relative;
}
.cta-link span {
  font-family: "Lato-bold";
  letter-spacing: 1px;
  background-color: transparent;
  position: absolute;
  bottom: 12px;
  left: 50px;
  font-size: 13px;
}
.cta-link img {
  background-color: transparent;
  width: 16px;
  position: absolute;
  bottom: 11px;
  left: 20px;
}
.cta-link:hover span {
  text-decoration: underline;
}

.hero-image img {
  width: 300px;
  margin-top: 120px;
  margin-left: 40px;
}

/* Large Device (1024px - 1200px) */
@media screen and (max-width: 1200px) {
  .hero-image {
    width: 45%;
  }
  .hero-text {
    width: 55%;
  }
  .cta {
    flex-direction: row;
  }
}

/* Medium device (768px - 1024px) */
@media screen and (max-width: 1024px) {
  .hero-image {
    display: none;
  }
  .hero-text {
    width: 100%;
  }
}

/* Small device (480px - 768px) */
@media screen and (max-width: 768px) {
  .hero-image {
    display: none;
  }
  .hero-text {
    width: 100%;
  }
}

/* Extra small device (0px - 480px) */
@media screen and (max-width: 480px) {
  .hero-image {
    display: none;
  }
  .hero-text {
    width: 100%;
  }
  .cta {
    flex-direction: column;
  }
  .cta-btn img {
    left: 35%;
  }
  .cta-btn span {
    left: 45%;
  }
  .cta-link {
    margin-top: 20px;
    margin-left: 0;
  }
  .cta-link img {
    left: 30%;
  }
  .cta-link span {
    left: 40%;
  }
}
