/* === Variables === */
:root {
  /* brown shades */
  --clr-brwn-1: #dfdad9;
  --clr-brwn-2: #c0af9e;
  --clr-brwn-3: #6d544a;
  --clr-brwn-4: #4c3a36;
  /* deep dark blue */
  --clr-blue-1: #202436;
  /* white */
  --clr-whit-1: #ffffff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lato-regular";
}

.root {
  overflow: hidden;
}

.main {
  position: relative;
  min-height: 100vh;
  background-color: var(--clr-blue-1);
}

.wrapper {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  max-width: 1200px;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.subheader-white {
  color: var(--clr-whit-1);
  font-family: "Lato-black";
  font-size: 30px;
  text-align: center;
}
.subheader-brown {
  color: var(--clr-brwn-2);
  font-family: "Lato-black";
  font-size: 30px;
  text-align: center;
}
.subheader-dark-brown {
  color: var(--clr-brwn-3);
  font-family: "Lato-black";
  text-align: center;
}

/* === Fonts === */
@font-face {
  font-family: "Lato-black";
  src: local("Lato"), url(./fonts/Lato/Lato-Black.ttf);
}
@font-face {
  font-family: "Lato-bold";
  src: local("Lato"), url(./fonts/Lato/Lato-Bold.ttf);
}
@font-face {
  font-family: "Lato-regular";
  src: local("Lato"), url(./fonts/Lato/Lato-Regular.ttf);
}
@font-face {
  font-family: "Lato-light";
  src: local("Lato"), url(./fonts/Lato/Lato-Light.ttf);
}
@font-face {
  font-family: "Lato-thin";
  src: local("Lato"), url(./fonts/Lato/Lato-Thin.ttf);
}
