.stacks {
  width: 100%;
  padding: 100px 40px;
  margin: 0 auto;
}

.stacks-container {
  margin: 100px auto;
  width: 45%;
}

.stacks-slider {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  font-family: "Lato-bold";
  color: var(--clr-whit-1);
  font-size: 20px;
  letter-spacing: 1px;
  text-align: center;
}

.stacks-status {
  color: var(--clr-brwn-2);
  font-size: 15px;
}

.stacks-slider img {
  position: absolute;
  width: 25px;
  background-color: transparent;
  /* Transition */
  -webkit-transition: width 0.1s linear;
  -moz-transition: width 0.1s linear;
  -ms-transition: width 0.1s linear;
  -o-transition: width 0.1s linear;
  transition: width 0.1s linear;
}
.stacks-slider img:hover {
  cursor: pointer;
  width: 27px;
}
.left-slider {
  left: 20%;
  bottom: 8.5px;
}
.right-slider {
  right: 20%;
  bottom: 8.5px;
}

.stacks-icons {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  background-color: var(--clr-whit-1);
  height: 85px;
  border-radius: 10px;
  margin-top: 50px;
  padding: 10px 10px;
}

.stacks-language {
  background: var(--clr-whit-1);
  width: 65px;
  height: 65px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05), 0px 10px 20px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  border: 2px solid var(--clr-brwn-2);
  text-align: center;
}

.stacks-language img {
  width: 40px;
  background: transparent;
  margin-top: 7px;
}

.stacks-tech {
  width: 70%;
  background-color: transparent;
  display: flex;
  justify-content: space-around;
  flex-direction: row;
}

/* Large device (1024px - 1200px) */
@media screen and (max-width: 1200px) {
  /* none */
}

/* Medium device (768px - 1024px) */
@media screen and (max-width: 1024px) {
  .stacks-container {
    width: 60%;
  }
}

/* Small device (480px - 768px) */
@media screen and (max-width: 768px) {
  .stacks-container {
    width: 80%;
  }
}

/* Extra small device (0px - 480px) */
@media screen and (max-width: 480px) {
  .stacks-container {
    width: 100%;
  }
  .left-slider {
    left: 5%;
  }
  .right-slider {
    right: 5%;
  }
}
