.projects {
  width: 100%;
  padding: 100px 40px;
  margin: 0 auto;
}

.projects-container {
  width: 100%;
  margin: 100px auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.project-container {
  width: 250px;
  height: 340px;
  background-color: var(--clr-whit-1);
  border-radius: 10px;
  text-align: center;
}
.project-container img {
  width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.project-container h3 {
  color: var(--clr-brwn-4);
  font-family: "Lato-bold";
  letter-spacing: 0.5px;
  font-size: 11px;
  margin-top: 20px;
}

.project-status {
  background-color: var(--clr-brwn-1);
  width: 50%;
  padding: 5px 0px;
  margin: 10px auto;
  border-radius: 10px;
}
.project-status h4 {
  color: var(--clr-whit-1);
  font-family: "Lato-bold";
  letter-spacing: 0.5px;
  font-size: 10px;
}

.project-container h5 {
  color: var(--clr-brwn-4);
  font-family: "Lato-bold";
  letter-spacing: 0.5px;
  font-size: 9px;
  margin-top: 18px;
}

.project-links {
  color: var(--clr-brwn-2);
  font-family: "Lato-bold";
  font-size: 11px;
  margin-top: 10px;
}
.project-links a {
  color: var(--clr-brwn-2);
  font-family: "Lato-bold";
  text-decoration: none;
}
.project-links a:hover {
  text-decoration: underline;
  color: var(--clr-brwn-3);
}

/* Large & Medium device (1024px - 1200px) */
@media screen and (max-width: 1200px) {
  /* none */
}

/* Small & Extra small device (0px - 768px) */
@media screen and (max-width: 768px) {
  .projects-container {
    flex-direction: column;
  }
  .project-container {
    margin: 20px auto;
  }
}
