.tools {
  width: 100%;
  padding: 100px 40px;
  margin: 0 auto;
}

.tools-container {
  text-align: center;
  background-color: var(--clr-whit-1);
  height: 500px;
  width: 84%;
  margin: 0px auto;
  padding: 50px 0px;
  border-radius: 10px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05), 0px 10px 20px rgba(0, 0, 0, 0.1);
  position: relative;
  z-index: 10;
}

.tools-image {
  width: 50%;
  margin: 7% auto;
}

.tools-image img {
  width: 40px;
  float: left;
  margin-left: 10%;
  margin-top: 50px;
}

/* Large device (1024px - 1200px) */
@media screen and (max-width: 1200px) {
  /* none */
}

/* Medium device (768px - 1024px) */
@media screen and (max-width: 1024px) {
  .tools-image img {
    width: 35px;
  }
}

/* Small device (480px - 768px) */
@media screen and (max-width: 768px) {
  .tools-image {
    width: 60%;
  }
  .tools-image img {
    width: 30px;
    margin-left: 9.5%;
  }
}
